export default {
	components:{
	},
	data() {
		return {
			page:1, //默认第一页
			refresh_tag:1, //开始加载数据的时候是0 加载完改为1  枷锁操作 
			list:[],	
			nearshop:{},
		}
	},
	computed:{
		
	},
	mounted() {
		
	},
	methods: {
		
		async loadData(lng,lat,cb)
		{
			this.$H.get('/mannerdish/index/index?lng='+lng+'&lat='+lat,{},{
				// 这里测试先不用token				
				token:false,
			}).then((res)=>{
				// 最近店铺
				this.data = res
				//最近店铺
				this.nearshop = this.data[0]
				//删除下标为0，长度为1的一个数据
				let current_shop = this.data.splice(0,1)
				//其他店铺
				this.list = this.data
				//关闭加载框
				setTimeout(function () {
				    uni.hideLoading();
				}, 1000);
				//执行回调 如果有的话
				cb && cb(res)
			})				
		},	
			
	    /**
		 * 去店铺下单
		 */
		setCurrentShop(index){
		  //1 设置当前店铺到缓存
		  this.$Util.setCache("current_shop",this.list[index]);
		  //2 跳转到菜单页面
		  //跳转导选择店铺的页面
		  uni.switchTab({
		  	url:"../../pages/mannerlist/mannerlist"
		  })
		},
		onLoad(){		
				//这时候that指的是全局，在内部函数想用全局的时候就得用that
				var that = this;
				// 获取当前坐标
				wx.getLocation({
				    type:'wgs84',
				    success: function (res) {
						// 打印一下获取的经纬度
						uni.showLoading({
						    title: '加载中'
						});
						// 请求后台数据
						that.loadData( res.longitude, res.latitude,(res)=>{
						    // 将获取的data数据存入上边的List[]中
						    // that.List = res
						});
				    }
				});
				
			
		},

		//下拉刷新
		onPullDownRefresh:function(){
			console.log('刷新了')
		 },
		//触底事件
		onReachBottom:function(){
			 // 这里向后台请求触底刷新数据
			 console.log('触底了')
		 },
		
	},
	
}
